import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const loaderStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                height: 100%;
                width: 100%;
            }

            ${root} {
                display: var(--loader-display);
                align-items: var(--loader-align);
                justify-content: var(--loader-justify);
                gap: var(--loader-content-gap);
                width: 100%;
                height: 100%;
            }

            ${child('slot-wrapper')} {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                width: 100%;
                height: 100%;
                gap: 12px;
            }

            ${child('loader')} {
                display: var(--loader-slot-display, contents);
                animation: fadeIn 0.2s;
            }

            ${is('loaded', 'timeout')} {
                animation: fadeIn 0.2s;
            }

            @keyframes fadeIn {
                0% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                }
            }
        `,
    ];
};
